.wordstool .toolcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wordstool .toolcontainer .calculateBox .content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.wordstool .toolcontainer .wordsBtn {
  width: 88px;
  height: 34px;
  cursor: pointer;
  background: #ff9712;
  border-radius: 4px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  text-align: center;
  font-size: 16px;
}
.wordstool .toolcontainer .wordsBtn:active {
  background: rgba(255, 151, 18, 0.39);
}

:deep(.vxe-modal--header) {
  background: #ff9712;
  padding: 0 !important;
}

:deep(.vxe-modal--box) {
  border: none !important;
}

:deep(.vxe-modal--close-btn) {
  top: 12px;
  color: #ffffff;
}
:deep(.vxe-modal--close-btn):hover {
  color: #ffffff;
}

.wordstool {
  z-index: 99999 !important;
}
.wordstool .closeBtn {
  display: none;
}
.wordstool .toolcontainer {
  margin: 0 auto;
  width: 800px;
}
.wordstool .toolcontainer .collapseHeader {
  display: flex;
}
.wordstool .toolcontainer .collapseHeader .headerTitle {
  width: 96px;
  font-size: 24px;
  font-family: Microsoft YaHei;
  font-weight: bold;
  line-height: 31px;
  color: #000000;
}
.wordstool .toolcontainer .collapseHeader .setting {
  margin: 0 6px 2px 620px;
  width: 64px;
  font-size: 16px;
  font-family: Microsoft YaHei;
  line-height: 31px;
  color: #555555;
}
.wordstool .toolcontainer .calculateBox {
  width: 800px;
  height: 74px;
  background: rgba(240, 242, 246, 0.39);
  border-radius: 4px;
  display: flex;
}
.wordstool .toolcontainer .calculateBox .content {
  padding: 10px 20px;
}
.wordstool .toolcontainer .calculateBox .content .caption {
  height: 20px;
  font-size: 14px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 19px;
  color: #555555;
  margin-bottom: 9px;
}
.wordstool .toolcontainer .calculateBox .content .inputBox {
  width: 210px;
}
.wordstool .toolcontainer .calculateBox .content .rawRes {
  align-self: flex-end;
  font-size: 14px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 19px;
  color: #bbbbbb;
}
.wordstool .toolcontainer .calculateBox .content .trueRes {
  align-self: flex-end;
  font-size: 20px;
  font-family: Arial;
  font-weight: bold;
  line-height: 23px;
  color: #ff9712;
}
.wordstool .toolcontainer .calculateBox .divider {
  margin: 10px 2px;
  width: 1px;
  height: 54px;
  background: rgba(221, 221, 221, 0.39);
}
.wordstool .toolcontainer .wordsBox {
  width: 800px;
}
.wordstool .toolcontainer .wordsBox :deep(.el-textarea__inner) {
  height: 50px;
  border: 2px solid #ff800b;
}
.wordstool .toolcontainer .wordsTip {
  height: 21px;
  align-self: flex-start;
  margin: 10px;
  color: #ff4040;
}
.wordstool .toolcontainer .wordsBtn {
  width: 300px;
  height: 50px;
  border-radius: 6px;
  font-size: 16px;
  line-height: 50px;
}
.wordstool .closeSec {
  display: none;
}

.modifytool :deep(.vxe-modal--body) {
  padding: 30px;
}
.modifytool :deep(.vxe-modal--content) {
  padding: 0;
}
.modifytool .closeSec {
  display: block;
  position: absolute;
  background-color: #fff;
  width: 94px;
  height: 50px;
  top: 28px;
  right: 30px;
}
.modifytool .closeSec .close {
  cursor: pointer;
  width: 30px;
  height: 30px;
  margin: 10px 0 10px 55px;
  line-height: 1;
}

.wordsResult :deep(.vxe-modal--body) {
  padding: 30px;
}
.wordsResult :deep(.vxe-modal--content) {
  padding: 0;
}
.wordsResult .resultcontainer .topTitlie {
  width: 112px;
  height: 21px;
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 21px;
  color: #000000;
  margin-bottom: 20px;
}
.wordsResult .resultcontainer .topClose {
  position: absolute;
  cursor: pointer;
  top: 14px;
  right: 26px;
}
.wordsResult .resultcontainer .resultBox {
  display: flex;
}
.wordsResult .resultcontainer .resultBox .resultOne {
  width: 270px;
  padding: 10px;
  background: rgba(248, 248, 248, 0.39);
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection {
  margin-bottom: 10px;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .myfunction {
  margin: 5px 0 20px 0;
  padding: 0 10px 0 10px;
  color: #ff9712;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .myfunction span {
  cursor: pointer;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .myfunction span:first-child {
  margin-right: 120px;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .mykey {
  padding: 16px 10px 15px 10px;
  height: 20px;
  background: rgb(255, 255, 255);
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .mykey .currentKey {
  font-weight: 800;
  color: #ff4040;
}
.wordsResult .resultcontainer .resultBox .resultOne .topFunSection .mykey .keyNote {
  color: #999999;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  height: 630px;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .eachWord {
  width: 92%;
  display: flex;
  justify-content: space-between;
  background: white;
  padding: 16px 10px 15px 10px;
  margin-top: 10px;
  height: 19px;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .eachWord .eachKey {
  color: #333333;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .eachWord .eachNote {
  cursor: pointer;
  color: #cccccc;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .eachWord .eachNote:active {
  color: #ff9712;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .selected {
  width: 91%;
  border: 1px solid #ff9712;
  border-radius: 4px;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .selected .eachKey {
  font-weight: 800;
  color: #000000;
}
.wordsResult .resultcontainer .resultBox .resultOne .keyWordSection .selected .eachNote {
  color: #ff9712;
}
.wordsResult .resultcontainer .resultBox .resultTwo {
  width: 590px;
  padding: 0 20px;
  background: white;
}
.wordsResult .resultcontainer .resultBox .resultTwo .divider {
  width: 630px;
  height: 1px;
  background: rgba(238, 238, 238, 0.39);
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrFunSection {
  display: flex;
  justify-content: space-between;
  padding: 16px 0px 15px 0px;
  height: 50px;
  box-sizing: border-box;
  align-items: center;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrFun > span {
  cursor: pointer;
  color: #ff9712;
  margin: 0 10px 0 10px;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrFun > span:active {
  color: #f1f0ed;
  background-color: #ff9712;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection {
  overflow-y: auto;
  height: 700px;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .divider {
  width: 560px;
  height: 1px;
  background: rgba(238, 238, 238, 0.39);
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr .wordsBox {
  width: 100%;
  padding: 16px 10px 15px 0;
  height: 50px;
  align-items: flex-start;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  color: #333333;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr .wordsBox > div {
  width: 70%;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr .wordsBox > div:last-child {
  width: 8%;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr .wordsBox .copyBtn {
  cursor: pointer;
  color: #cccccc;
}
.wordsResult .resultcontainer .resultBox .resultTwo .arrListSection .allArr .wordsBox .copyBtn:active {
  color: #ff9712;
}

.el-select-dropdown__item.selected {
  color: #ff9712;
}

:deep(.el-collapse-item__wrap) {
  border: none;
}

:deep(.el-collapse-item__header) {
  border: none;
}

:deep(.el-collapse-item__content) {
  padding-bottom: 20px;
}

:deep(.el-collapse) {
  border: none;
}

:deep(.el-textarea__inner) {
  resize: none;
}

:deep(.wordstool .vxe-modal--body) {
  padding-top: 35px;
  padding-bottom: 30px;
}

.mybtn {
  position: absolute;
  top: 100px;
  left: 100px;
}