.jclLogin {
  position: fixed;
  z-index: 100001;
  left: 500px;
  top: 100px;
}
.jclLogin .el-dialog {
  border-radius: 5px;
  width: 380px;
}
.jclLogin .el-dialog .el-dialog__header {
  border-radius: 5px;
  margin: 0;
  padding: 26px 0 12px 0;
  background: linear-gradient(#ffebd6, #fff9f3);
}
.jclLogin .el-dialog .el-dialog__header .el-dialog__headerbtn {
  top: 18px;
  right: 10px;
}
.jclLogin .el-dialog .el-dialog__header .el-dialog__title {
  height: 26px;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}
.jclLogin .el-dialog .el-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.jclLogin .el-dialog .el-form .el-form-item {
  margin-bottom: 40px;
  width: 320px;
}
.jclLogin .el-dialog .el-form .el-form-item .el-form-item__content {
  display: block;
}
.jclLogin .el-dialog .el-form .el-form-item .el-form-item__content .el-input {
  height: 100%;
  font-size: 14px;
  color: #000000;
  background: rgba(248, 248, 248, 0.226);
}
.jclLogin .el-dialog .el-form .el-form-item .el-form-item__content .el-form-item__error {
  height: 19px;
  font-size: 14px;
  line-height: 24px;
  color: #ff4040;
  margin-top: 8px;
  left: 15px;
}
.jclLogin .el-dialog .el-form .submititem {
  margin-bottom: 5px;
}
.jclLogin .el-dialog .el-form .submititem .submitbtn {
  width: 320px;
  height: 40px;
  background: rgba(255, 152, 18, 0.932);
  border-radius: 4px;
  font-size: 20px;
  line-height: 40px;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
}
.jclLogin .el-dialog .el-dialog__body {
  background: linear-gradient(#fff9f3, #fffbf8);
  border-radius: 5px;
}
.jclLogin .el-dialog .codeSec {
  display: flex;
}
.jclLogin .el-dialog .codeSec .codeinput {
  border-right-style: none !important;
}
.jclLogin .el-dialog .codeSec .getcode {
  border-left-style: none;
  color: #ff9712;
}
.jclLogin .el-dialog .codeSec .getcode:hover {
  color: #ff9712;
  background-color: transparent;
  border: 1px solid #ff9712;
}
.jclLogin .el-dialog .codeSec .getcode:actived {
  color: #ff9712;
  background-color: transparent;
}
.jclLogin .el-dialog .el-button--warning.is-link {
  --el-button-text-color: #ff9712;
}
.jclLogin .el-dialog .loginFail {
  position: absolute;
  top: 227px;
  left: 55px;
  font-size: 14px;
  line-height: 30px;
  color: #ff0000;
}
.jclLogin .changeAccount {
  margin: 0 auto;
  width: 320px;
  height: 40px;
  background: rgba(255, 152, 18, 0.932);
  border-radius: 4px;
  font-size: 20px;
  line-height: 40px;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
}

:root {
  --el-color-primary: #ff9712;
}