
body {
  background: url('https://junchenlunoffice.oss-cn-shenzhen.aliyuncs.com/plugs/logo1/mask.png') no-repeat;
}

.container {
  width: 368px;
  height: 445px;
  opacity: 1;
  // chrome原生body界面无法覆写边框样式
  // background-clip: padding-box;
  // border: 1px solid #DDDDDD;
  border-radius: 4px;

  // background: url("/img/mask.png") no-repeat;
  .header {
    margin: 5px 0 20px 0;

    .section {
      text-align: center;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px;
      width: 328px;
      height: 94px;
      background: linear-gradient(180deg, #ffad44 0%, #ff9712 100%);
      opacity: 1;
      border-radius: 4px;

      a {
        width: 71px;
        // width: 56px;
        text-decoration: none;
        height: 19px;
        font-size: 14px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        line-height: 22px;
        color: #ffffff;
        opacity: 1;
      }
    }
  }

  .main {
    display: flex;
    flex-wrap: wrap;

    // margin-bottom: 16px;
    div {
      width: 25%;
      margin-bottom: 20px;

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-decoration: none;

        img {
          width: 38px;
          height: 38px;
        }

        span {
          font-family: Microsoft YaHei;
          margin-top: 8px;
          height: 19px;
          line-height: 22px;
          font-size: 14px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #000;
        }
      }
    }
  }

  .footer {
    margin: -10px;
    padding-bottom: 10px;

    .version {
      margin: 0 auto;
      width: 74px;
      height: 20px;
      font-size: 14px;
      font-family: Microsoft YaHei;
      font-weight: 400;
      line-height: 22px;
      color: #999999;
      opacity: 1;
    }
  }
}

